@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  height: 100%;
  background-color: #000000;
background-image: -webkit-linear-gradient(303deg, #0c0c0cc7 0%, #201e1e 74%);
background-image: linear-gradient(147deg, #0c0c0cc7 0%, #201e1e 74%);
  overflow: auto;
  text-align: center;
  font-family: 'Roboto Mono', monospace;
}

body h1 {
  color: white;
}

#root {
height: 100vh;
display: flex;
justify-content: center;

}


.App {
  text-align: center;
  box-sizing: border-box; 
  margin: 0;
  width: 70%;
  display: flex;
  flex-direction: column; 
}

.AddTask, .finished-task, .active-task {
  margin-bottom: 40px; 
  min-height: 20vh;
  color: #fff;
}

.finished-task, .active-task {
  background-color: #292626;
  border-radius: 20px;
  border: 1px solid;
}
#task-name, #date{
  height: 20%;
  width: 30%;
  padding: 10px;
  border: 1px solid;
  border-color: #610707;
  border-radius: 15px;
  line-height: 20px;

}



*:focus {
  outline: none;
}

#important, #date{
  margin-left: 40px;
}

.AddTask button{
  border-radius: 15px;
  width: 15%;
  height: 40%;
  padding: 10px 15px;
  color: #fff;
  border-color: #fff;
  background-color: #000000;
}

.AddTask button:hover{
  background-color: #292626;
}

.TaskList button{
  background-color: #000000;
  color: #fff;
  
  padding: 5px 10px;
  margin-left: 10px;
  border-radius: 15px;
}
